/*blog-overlay*/
.blog-overlay {
  position: relative;
  text-align: left;
  z-index: 2;
}
.blog-overlay:before {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 5%, rgba(0,0,0,0.09) 100%);
  content: "";
  left: 0;
  right: 0;
  z-index: 1;
}
.blog-overlay .blog-name {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 2;
  padding: 15px 15px 0;
}
.blog-overlay .blog-name a.tag{
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
}
.blog-overlay .blog-name h5 a{
  font-size: 24px;
}
.blog-overlay .blog-name h5:hover a{
  color: $white;
}
.blog-overlay.white-bg:before, .blog-overlay.dark-theme-bg:before, .blog-overlay.theme-bg:before {
  display: none;
}
.blog-overlay .blog-image {
  overflow: hidden;
  position: relative;
}



/*blog-overlay-01*/
.blog-overlay-02 {
  position: relative;
  text-align: left;
  z-index: 2;
}
.blog-overlay-02:before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  left: 0;
  right: 0;
  z-index: 1;
}
.blog-overlay-02 .blog-name {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  width: 90%;
  margin: 0 auto;
  width: 90%;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.8);
  padding:30px;
}
.blog-overlay-02 .blog-name a.tag{
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
}
.blog-overlay-02 .blog-name h5 a{
  font-weight: 500;
  font-size: 24px;
}
.blog-overlay-02.white-bg:before, .blog-overlay-02.dark-theme-bg:before, .blog-overlay-02.theme-bg:before {
  display: none;
}
.blog-overlay-02 .blog-image {
  overflow: hidden;
  position: relative;
}
.blog-post.blog-overlay-02 .blog-post-footer a{
  color: $gray-9;
}
.blog-post.blog-overlay-02 .blog-post-author span{
