/*blog-overlay-01*/
.blog-overlay-01 {
  position: relative;
  text-align: left;
  z-index: 2;
}
.blog-overlay-01 .blog-name {
  padding-left: 30px;
  position: absolute;
  bottom: -30px;
  right: 0;
  z-index: 2;
  background: $white;
  box-shadow: 0 2px 2px rgba($black, 0.1);
  width: 60%;
  text-align: right;
  padding: 15px 25px;
}
.blog-overlay-01 .blog-name a.tag{
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
}
.blog-overlay-01 .blog-name h5 a{
  font-weight: 500;
  font-size: 24px;
}
.blog-overlay-01.white-bg:before, .blog-overlay-01.dark-theme-bg:before, .blog-overlay-01.theme-bg:before {
  display: none;
}
.blog-overlay-01 .blog-image {
  overflow: hidden;
  position: relative;
  padding-right: 20px;
}

.blog-overlay-03{
  text-align: center;
  position: relative;
    .blog-name {
      h5{
        font-size: 20px;
        a{
          text-align: left;
          background: $white;
          display: inline-block;
          position: relative;
          padding: 15px 20px;
          top: -28px;
          width: 85%;
          margin: 0 auto;
          align-items:center;
          box-shadow: 0 2px 6px rgba($black, 0.1);
          img{
            width:40px;
            height: 40px;
            margin-right: 15px;
          }
      }
    }
  }
}

.swiper-container.editor{
  .blog-post{
    .blog-image img{
      height: auto;
    }
    .blog-name{
      padding-left: 0px;
      h1{
        font-size: 16px;
        line-height: 26px;
        padding: 0px 15px;
      }
      .blog-post-footer{padding-bottom: 0px;padding-top: 10px;}
      a.tag{
            padding: 8px 15px;
      }
    }
  }
}



.advertise{
