body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #999999;
  overflow-x: hidden; }

a {
  outline: medium none !important;
  color: #719367;
  transition: all 0.3s ease-in-out;
  text-decoration: none !important; }
  a:focus {
    color: #719367;
    text-decoration: none !important; }
  a:hover {
    color: #719367;
    text-decoration: none !important; }

input {
  outline: medium none !important;
  color: #719367; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: normal;
  color: #333333;
  margin-top: 0px;
  text-transform: capitalize; }
  h1 a,
  h2 a,
  h3 a,
  h4 a,
  h5 a,
  h6 a {
    color: inherit; }

label {
  font-weight: normal; }

h1 {
  font-size: 36px;
  font-style: normal;
  font-weight: 700; }

h2 {
  font-size: 30px;
  font-weight: 700; }

h3 {
  font-size: 28px;
  font-weight: 700; }

h4 {
  font-size: 26px;
  font-weight: 600; }

h5 {
  font-size: 24px;
  font-weight: 500; }

h6 {
  font-size: 18px;
  font-weight: 500; }

p {
  font-weight: normal;
  line-height: 26px; }

*::-moz-selection {
  background: #719367;
  color: #ffffff;
  text-shadow: none; }

::-moz-selection {
  background: #719367;
  color: #ffffff;
  text-shadow: none; }

::selection {
  background: #719367;
  color: #ffffff;
  text-shadow: none; }

@media (min-width: 1200px) {
  .container {
