/* container-fluid */
.container-fluid.container-space {
  padding: 0 100px; }

/* form-control */
.form-control {
  border: 1px solid #e7e7e7;
  border-radius: 0px;
  padding: 10px 20px;
  box-shadow: none;
  font-size: 14px;
  font-weight: 500;
  color: #333333;
  background-clip: inherit !important;
  background: #f6f6f6;
  border-radius: 50px; }
  .form-control:focus {
    box-shadow: none;
    border-color: #719367; }

.form-control::-moz-placeholder {
  color: #333333; }

.form-control::-ms-input-placeholder {
  color: #333333; }

.form-control::-webkit-input-placeholder {
  color: #333333; }

input.form-control {
  height: 50px; }

/* Inner Header Shape */
.cls-1 {
  fill: #fff;
  fill-rule: evenodd; }

.cls-1-dark {
  fill: #18191c;
  fill-rule: evenodd; }

/* Dark Form */
.dark-form h6 {
  color: rgba(255, 255, 255, 0.5);
  font-weight: 500; }

.dark-form .form-control {
  background-color: #bbbbbb;
  border-color: #bbbbbb;
  color: rgba(255, 255, 255, 0.4);
  font-weight: normal; }

.dark-form .form-control::-moz-placeholder {
  color: rgba(255, 255, 255, 0.4); }

.dark-form .form-control::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.4); }

.dark-form .form-control::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.4); }

.dark-form .select2-container--default .select2-selection--single {
  background-color: #bbbbbb; }

.dark-form .select2-container--default .select2-selection--single .select2-selection__rendered {
  color: rgba(255, 255, 255, 0.4);
  font-weight: normal; }

.dark-form .select2-container--default .select2-selection--single .select2-selection__arrow:before {
  color: rgba(255, 255, 255, 0.4); }

.dark-page .select2-container--open .select2-dropdown {
  background: #bbbbbb;
  border-color: #bbbbbb; }

.dark-page .select2-container--default .select2-results > .select2-results__options {
  color: rgba(255, 255, 255, 0.4); }

.dark-page .select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: #000000;
  color: rgba(255, 255, 255, 0.4); }

.dark-page .select2-container--default .select2-results__option[aria-selected=true] {
  background-color: #000000;
  color: rgba(255, 255, 255, 0.4); }

.dark-page .select2-container--default .select2-search--dropdown .select2-search__field {
  background-color: #000000;
  border-color: #000000;
  color: rgba(255, 255, 255, 0.4); }

/* custom-file */
.custom-file {
  height: 50px; }
  .custom-file .custom-file-input {
    height: 50px;
    color: #333333;
    border-radius: 50px;
    border-color: #e7e7e7; }
    .custom-file .custom-file-input:focus {
      box-shadow: none;
      border-color: #719367; }
  .custom-file .custom-file-label {
    height: 50px;
    font-size: 14px;
    font-weight: 600;
    color: #333333;
    border-color: #e7e7e7;
    padding: 15px 90px 15px 20px;
    border-radius: 50px; }
    .custom-file .custom-file-label:after {
      height: 48px;
      padding: 14px 25px; }

.custom-file-input:focus ~ .custom-file-label {
  box-shadow: none;
  border-color: #719367; }

/* input-group-prepend */
.input-group-prepend .input-group-text,
.input-group-append .input-group-text {
  width: 50px;
  background: transparent;
  border-color: #e7e7e7;
  box-shadow: none;
  justify-content: center; }

/* checkbox */
.custom-control-input:checked ~ .custom-control-label:before {
  background: #719367;
  border-color: #719367; }

.custom-control-input:not(:disabled):active ~ .custom-control-label:before {
  background: transparent;
  border-color: transparent; }

.custom-control-input:focus ~ .custom-control-label:before {
  box-shadow: none; }

.custom-control-input:focus:not(:checked) ~ .custom-control-label:before {
  border-color: #e7e7e7; }

.custom-control-label:before {
  top: 2px; }

.custom-control-label:after {
  top: 2px; }

.custom-checkbox .custom-control-label:before {
  border-radius: 50px;
  border: 2px solid #dfdfdf; }

/* border  */
.border {
  border-color: #e7e7e7 !important; }

.border-top {
  border-top-color: #e7e7e7 !important; }

.border-left {
  border-left-color: #e7e7e7 !important; }

.border-right {
  border-right-color: #e7e7e7 !important; }

.border-bottom {
  border-bottom-color: #e7e7e7 !important; }

/* badge  */
.badge {
  border-radius: 50px; }
  .badge + .badge {
    margin-left: 6px; }

.badge-md {
  padding: 5px 8px;
  font-size: 13px;
  font-weight: normal; }

.badge-primary {
