/* back-to-to */
.back-to-top a {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 50px;
  right: 40px;
  width: 50px;
  height: 40px;
  margin: 0px;
  color: #ffffff;
  font-size: 18px;
  background: #719367;
  color: #ffffff;
  transition: all 0.3s ease-in-out;
  z-index: 999; }
  .back-to-top a:hover {
    color: #ffffff; }
  .back-to-top a:focus {
    color: #ffffff; }

/* pagination */
.pagination .page-item .page-link {
  padding: 14px 28px;
  color: #999999;
  border-color: #e7e7e7;
  margin: 0 4px; }
  .pagination .page-item .page-link span {
    display: block; }
  .pagination .page-item .page-link:focus {
    box-shadow: none; }

.pagination .page-item.active .page-link {
  background: #719367;
  border-color: #719367;
  color: #ffffff; }

.pagination .page-item:first-child .page-link {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0; }

.pagination .page-item:last-child .page-link {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0; }

/* blockquote */
.blockquote {
  font-size: 16px;
  font-style: italic;
  display: block;
  padding: 50px;
  background: #f6f6f6;
  border: 1px solid #e7e7e7;
  position: relative; }
  .blockquote i {
    z-index: 0;
    font-size: 30px;
    line-height: 30px; }

iframe {
  width: 100%; }

/* table */
.table {
  border-spacing: 0 1px;
  font-size: 16px;
  font-weight: 600; }
  .table td {
    padding: 14px 20px; }
  .table th {
    padding: 14px 20px; }

.table-bordered td {
  border-color: #e7e7e7; }

.table-bordered th {
  border-color: #e7e7e7; }

.table-striped tbody tr:nth-of-type(odd) {
  background: #040404; }
  .table-striped tbody tr:nth-of-type(odd) td {
    color: #ffffff; }

.table-striped .avatar {
  margin-right: 15px; }

.table-striped tr td {
  color: #ffffff; }

.table td,
.table th {
  vertical-align: middle; }

.table-skew {
  padding: 0 30px; }

@media (max-width: 767px) {
  .table td,
  .table th {
    white-space: nowrap; } }

